@import url("https://fonts.googleapis.com/css2?family=Gentium+Book+Plus:ital@1&family=Open+Sans:wght@500&family=Source+Sans+Pro:wght@300&display=swap");

body {
    margin: 0;
    font-family: "NTR", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #0a192f;
    text-align: center;
    line-height: 1.5;
    font-size: 0.875rem;
}

code {
    font-family: "NTR", sans-serif;
}

h1 {
    font-family: "NTR", sans-serif;
    color: #64ffda;
}

.card {
    width: 50vw;
    padding: 40px;
    border-radius: 15px;
    margin: 5rem auto;
    background-color: #64ffda;
}

p {
    color: #0a192f;
    font-size: 1.5rem;
    text-decoration: italic;
    font-family: "NTR", sans-serif;
    ;
}

.type {
    color: #0a192f;
    text-transform: uppercase;
    font-family: "NTR", sans-serif;
    ;
}

.button {
    cursor: pointer;
    background-color: #0a192f;
    padding-top: 10px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 10px;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    font-family: "NTR", sans-serif;
    ;
    text-transform: uppercase;
    color: white;
    font-weight: 700;
}

.button:hover {
    background-color: #0a192ff3;
    color: grey
}